<template><section>

<h1>单选按钮 Radio</h1>
<h3>基础用法</h3>
<p>由于选项默认可见，不宜过多，若选项过多，建议使用 Select 选择器。</p>
<demo-block>
                            <div class="source" slot="source">
  <as-row>
    <as-radio v-model="radio1" label="1">单选</as-radio>
  </as-row>
  <as-row>
    <as-radio v-model="radio1" label="1">单选1</as-radio>
    <as-radio v-model="radio1" label="2">单选2</as-radio>
    <as-radio v-model="radio1" label="3">单选3</as-radio>
  </as-row>

  

</div>
