    <span class="hljs-tag">&lt;<span class="hljs-name">as-radio</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"radio1"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"2"</span>&gt;</span>单选2<span class="hljs-tag">&lt;/<span class="hljs-name">as-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">as-radio</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"radio1"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"3"</span>&gt;</span>单选3<span class="hljs-tag">&lt;/<span class="hljs-name">as-radio</span>&gt;</span>
