  <as-row>
    <as-radio v-model="radio1" label="1" border="">单选</as-radio>
  </as-row>
  <as-row>
    <as-radio v-model="radio1" label="1" border="">单选1</as-radio>
    <as-radio v-model="radio1" label="2" border="">单选2</as-radio>
