    <as-radio v-model="radio2" label="lg" size="lg" border="">lg超大</as-radio>
    <as-radio v-model="radio2" label="md" size="md" border="">md默认</as-radio>
    <as-radio v-model="radio2" label="sm" size="sm" border="">sm小号</as-radio>
    <as-radio v-model="radio2" label="xs" size="xs" border="">xs超小</as-radio>
