    <as-radio v-model="radio2" label="sm" size="lg" button="">lg超大</as-radio>
    <as-radio v-model="radio2" label="xs" size="lg" button="">lg超大</as-radio>
  </as-row>
  <as-row>
    <as-radio v-model="radio2" label="lg" size="md" button="">md默认</as-radio>
    <as-radio v-model="radio2" label="md" size="md" button="">md默认</as-radio>
    <as-radio v-model="radio2" label="sm" size="md" button="">md默认</as-radio>
    <as-radio v-model="radio2" label="xs" size="md" button="">md默认</as-radio>
  </as-row>
  <as-row>
    <as-radio v-model="radio2" label="lg" size="sm" button="">sm小号</as-radio>
