  </as-row>
  <as-row>
    <as-radio v-model="radio2" label="lg" size="xs" button="">xs超小</as-radio>
    <as-radio v-model="radio2" label="md" size="xs" button="">xs超小</as-radio>
    <as-radio v-model="radio2" label="sm" size="xs" button="">xs超小</as-radio>
