<h3>单选框组【推荐】</h3>
<p>适用于在多个互斥的选项中选择的场景, 一般建议使用该方式使用</p>
<demo-block>
                            <div class="source" slot="source">
  <as-radio-group v-model="radio1">
    <as-radio label="1">单选1</as-radio>
    <as-radio label="2">单选2</as-radio>
    <as-radio label="3">单选3</as-radio>
  </as-radio-group>
  <as-radio-group v-model="radio1">
    <as-radio label="1"><as-icon name="sexw"></as-icon></as-radio>
    <as-radio label="2"><as-icon name="sexm"></as-icon></as-radio>
  </as-radio-group>
  <as-radio-group v-model="radio1">
    <as-radio label="1" border=""><as-icon name="sexw"></as-icon></as-radio>
    <as-radio label="2" border=""><as-icon name="sexm"></as-icon></as-radio>
  </as-radio-group>
  <as-radio-group v-model="radio2">
    <as-radio label="lg" size="md" button="">md默认</as-radio>
    <as-radio label="md" size="md" button="">md默认</as-radio>
    <as-radio label="sm" size="md" button="">md默认</as-radio>
    <as-radio label="xs" size="md" button="">md默认</as-radio>
  </as-radio-group>

  

</div>
