<h2>属性说明</h2>
<h3>Radio参数</h3>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>size</td>
<td>大小</td>
<td>string</td>
<td>lg/md/sm/xs</td>
<td>md</td>
</tr>
<tr>
<td>border</td>
<td>是否为边框单选框</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>button</td>
<td>是否为按钮单选框</td>
<td>boolean</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>disabled</td>
<td>是否可用</td>
<td>boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>label</td>
<td>选项</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>name</td>
<td>原生 name 属性</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
</tbody>
</table>
<h3>Radio Event参数</h3>
<table>
<thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>绑定值变化时触发的事件</td>
<td>选中的 Radio label 值</td>
</tr>
</tbody>
</table>
